import './search.scss'
import { Message } from 'element-ui'

export default {
  data() {
    return {
      show: false,
      lists: [
        { name: '', state: 1, msg: '' },
        { name: '', state: 2, msg: '' },
        { name: '', state: 3, msg: '' },
        { name: '', state: 4, msg: '' }
      ],
      keyword: '',
      curIndex: 1
    }
  },
  computed: {
    curList() {
      return this.lists[this.curIndex]
    }
  },
  methods: {
    toggle() {
      this.show = true
    },
    change(index) {
      this.curIndex = index
      this.show = false
    },
    search: function() {
      if (!this.keyword) {
        Message('')
        return
      }
      location.href = `productLists.html?state=${this.curList.state}&keyword=${this.keyword}`
    }
  }
}




