class Utils {

  /**
   * urlquery
   * @param {string} url url
   * @param {Object} data ,
   * @return {string} url
   * tododatavalue
   */
  static setQuery(url, data) {
    let str = ''
    Object.keys(data).forEach(key => {
      str += `&${key}=${data[key]}`
    })
    let mark = url.indexOf('?') == -1 ? '?' : '&'
    return url + mark + str.substr(1)
  }

  /**
   * url
   * @param {string} key querykeyundefined
   * @return {string|Object} keykvaluequeryobject
   */
  static getQuery(key = undefined) {
    let arr = location.search.substr(1).split('&')
    return this.arrToObj(key, arr)
  }

   /**
   * 
   * var router = '/user/:name/:age';
   * var path = '/user/abc/def';
   */
  static getPath(router, path) {
    var routerArr = router.split('/');
    var pathArr = path.split('/');
    var obj = {};
    for (var i = 0; i < routerArr.length; i++) {
      var item = routerArr[i];
      if (item[0] === ':') {
        obj[item.slice(1)] = pathArr[i];
      }
    }
  }

  /*
   * cookie
   * @param {string} cname cookiename
   * @return {string} cookievaluecookienamenamevalue
   */
  static getCookie(cname) {
    let arr = document.cookie.split(';')
    return this.arrToObj(cname, arr)
  }

  static isLogin() {
    return (this.getCookie('token') ? true : false)
  }


  static arrToObj(key, arr) {
    let data = {}
    arr.forEach((item) => {
      let cur = item.split('=')
      if(cur[0]==''){

      }else{
        data[cur[0].trim()] = cur[1].trim()
      }
      
    })
    return key ? data[key] : data
  }
}

export default Utils


